import React from 'react';

// Este componente slo proporciona un botn de RESET para recargar la pgina

function BotonReset() {
  const recargaPagina = () => {
    window.location.reload();
  };

  return (
    <button className="button" onClick={recargaPagina}>RESET</button>
  );
}

export default BotonReset;